import React from "react";
import tw, { styled } from "twin.macro";
import { filterObject } from "../util/common";

const LineUl = styled.ul`
  ${tw`flex w-max`}
  border-bottom: #c5c5c5 solid .5px;
  color: #707070;

  li {
    ${tw`opacity-50`}

    button {
      ${tw`focus:outline-none font-light`}
    }

    &:not(:last-child) {
      ${tw`mr-3`}
    }

    &.active {
      ${tw`opacity-100`}
      border-bottom: #378BF3 solid 2px;

      button {
        ${tw`font-medium`}
      }
    }
  }
`;

const ButtonsUl = styled.ul`
  ${tw`flex flex-wrap`}

  li {
    ${tw`opacity-50 flex flex-col justify-center mb-3`}

    button {
      ${tw`whitespace-nowrap focus:outline-none font-medium block text-center text-black bg-gray-200 p-3 rounded-md hover:bg-gray-300 self-end`}
    }

    &:not(:last-child) {
      ${tw`mr-3`}
    }

    &.active {
      ${tw`opacity-100`}

      button {
        ${tw`focus:outline-none whitespace-nowrap text-white bg-blue-400 hover:bg-blue-400`}
      }
    }
  }
`;

type SelectActionProps = React.HTMLProps<HTMLUListElement> & {
  options: string[];
  onChangeOption?: (option: string, index: number) => void;
  initialValue?: number;
  buttons?: boolean;
  asInput?: string;
};

export function SelectAction({
  options,
  onChangeOption,
  initialValue = 0,
  buttons = false,
  asInput,
  ...props
}: SelectActionProps) {
  const initialOption =
    initialValue < options.length && initialValue > 0 ? initialValue : 0;
  const [selectedOption, setSelectedOption] = React.useState(initialOption);

  const selectOption = (i: number) => {
    setSelectedOption(i);
    onChangeOption && onChangeOption(options[i], i);
  };

  const Ul = buttons ? ButtonsUl : LineUl;

  return (
    <Ul {...filterObject(props, ["as"])}>
      {asInput && (
        <input
          type="hidden"
          id={asInput}
          name={asInput}
          value={options[selectedOption]}
        />
      )}
      {options.map((option, i) => (
        <li key={i} className={selectedOption === i ? "active" : ""}>
          <button type="button" onClick={() => selectOption(i)}>
            {option}
          </button>
        </li>
      ))}
    </Ul>
  );
}
