function CustomerActionSelect(props: React.HTMLProps<HTMLUListElement>) {
  const {
    customerActions,
    customerActionsNames,
    initialCustomerAction,
    changeSlide,
  } = useSlides();

  return (
    <SelectAction
      asInput="type"
      options={customerActionsNames}
      initialValue={customerActions.indexOf(initialCustomerAction)}
      onChangeOption={(action) =>
        changeSlide(
          customerActions[
            customerActionsNames.indexOf(action as CustomerActionName)
          ]
        )
      }
      {...props}
    />
  );
}
