import React from "react";
import InputMask from "react-input-mask";

type InputProps = React.HTMLProps<HTMLInputElement> & {
  label: string;
  mask?: string;
  maskPlaceholder?: string;
  icon?: JSX.Element;
};

export function Input(props: InputProps) {
  const { label, icon, className, style, ref, ...inputProps } = props;

  return LabelInput({
    ...props,
    input: (
      <div className="flex items-center rounded-md p-4 mt-3 bg-gray-100 w-full">
        {props.icon}
        {props.mask ? (
          <InputMask
            {...inputProps}
            mask={props.mask}
            maskPlaceholder={props.maskPlaceholder}
            className="focus:outline-none bg-gray-100 w-full"
          />
        ) : (
          <input
            {...inputProps}
            className="focus:outline-none bg-gray-100 w-full"
          />
        )}
      </div>
    ),
  });
}

export type LabelInputProps = InputProps & { input: JSX.Element };

export function LabelInput(props: LabelInputProps) {
  return (
    <div className={props.className} style={props.style}>
      <label htmlFor={props.name} className="block font-medium text-gray-500">
        {props.label}
      </label>
      {props.input}
    </div>
  );
}
