/* eslint-disable @typescript-eslint/no-empty-function */
import { createContext, useContext, useReducer } from "react";
import { useURLParams } from "../../util/useURLParams";
import {
  SlidesProviderState,
  reducer,
  changeSlide,
  mergeFormData,
  initialState,
} from "./reducer";
import {
  CustomerAction,
  CustomerActionName,
  customerActions,
  customerActionsNames,
  SlideName,
  SlideRecord,
  slides,
} from "./slides";

export interface SlidesContextData extends SlidesProviderState {
  slides: SlideRecord;
  customerActions: CustomerAction[];
  customerActionsNames: CustomerActionName[];
  initialCustomerAction: CustomerAction;
  changeSlide: (slide: SlideName) => void;
  mergeFormData: (formData: FormData) => void;
}

const SlidesContext = createContext<SlidesContextData>({} as SlidesContextData);

type Provider<T> = React.FC<{ children: JSX.Element[] | JSX.Element }> & {
  Consumer: React.Consumer<T>;
};

export const SlidesProvider: Provider<SlidesContextData> = (props) => {
  const { get: getURLParam } = useURLParams();
  const actionParam = getURLParam("action");

  const reducerInitialState = initialState({ actionParam });
  const [state, dispatch] = useReducer(reducer, reducerInitialState);

  const { currentSlideName, currentSlide, formData } = state;

  return (
    <SlidesContext.Provider
      value={{
        slides,
        currentSlide,
        currentSlideName,
        customerActions,
        customerActionsNames,
        initialCustomerAction: reducerInitialState.currentSlideName as CustomerAction,
        formData,
        changeSlide: changeSlide(dispatch),
        mergeFormData: mergeFormData(dispatch),
      }}
      {...props}
    />
  );
};

SlidesProvider.Consumer = SlidesContext.Consumer;

export const useSlides = (): SlidesContextData => useContext(SlidesContext);
