import React from "react";
import { useSlides } from "../providers/SlidesProvider";

export function FormSlides() {
  const slidesData = useSlides();
  const { currentSlide } = slidesData;

  const sideInfoRef = React.useRef<HTMLDivElement>(null);

  /*
  const retriggerSideInfoAnimation = () =>
    sideInfoRef.current?.parentNode?.replaceChild(
      sideInfoRef.current,
      sideInfoRef.current
    );
  */

  if (currentSlide.onLoad) {
    currentSlide.onLoad(slidesData);
  }

  const onSubmitForm = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();

    const formData = new FormData(e.target as HTMLFormElement);
    currentSlide.form?.onSubmit(formData, slidesData);
  };

  const buttonClassesFn =
    currentSlide.form?.submitButtonOverrideClasses ||
    ((classes: string) => classes);

  return (
    <main
      className={`relative w-full h-full gradient-main ${
        currentSlide.form ? "h-full" : "h-screen"
      } ${currentSlide.backgroundClass}`}
    >
      <div className="absolute py-10 px-20 flex lg:justify-start justify-center w-full">
        <a href="/">
          <object
            type="image/svg+xml"
            data="certifica-facil-logo.svg"
            style={{ pointerEvents: "none" }}
          >
            Uperttech Certifica Fácil
          </object>
        </a>
      </div>
      <div className="flex lg:flex-row flex-col items-center max-w-screen-2xl w-full h-full m-auto">
        <div
          className="flex-1 items-center justify-center lg:block text-white text-lg m-5 lg:mx-12 lg:p-0 pt-28 px-16 w-full max-w-screen-lg fade-in"
          ref={sideInfoRef}
        >
          <h1 className="text-5xl font-medium mb-5">
            {currentSlide.sideInfo.title}
          </h1>

          {currentSlide.sideInfo.content.map((text, i) => (
            <p className="mb-5 opacity-80" key={i}>
              {text}
            </p>
          ))}
        </div>

        {currentSlide.form && (
          <div
            className="bg-white shadow-lg lg:mx-12 max-w-2xl my-10 mx-5 rounded-3xl p-10 flex flex-col"
            style={{ height: "90%" }}
          >
            <h2 className="font-semibold text-3xl mt-8 mb-10">
              {currentSlide.form.title}
            </h2>

            <form
              onSubmit={onSubmitForm}
              className="flex flex-1 flex-col"
              id="form"
            >
              {currentSlide.form.inputs.map((el, i) =>
                React.cloneElement(el, { key: el.props.name || i })
              )}

              <input type="hidden" name="_grct" id="_grct" />

              <div className="flex-1 flex flex-col justify-end">
                <button
                  className={buttonClassesFn(
                    "g-recaptcha block text-center text-white bg-blue-400 py-3 px-5 duration-300 rounded-md hover:bg-blue-700 self-end focus:outline-none"
                  )}
                  type="submit"
                >
                  {currentSlide.form.submitButtonText}
                </button>

                <div className="mt-8 text-xs font-light text-gray-300">
                  Protegido por reCAPTCHA e sujeito à{"\u00A0"}
                  <a
                    className="text-gray-400 font-medium hover:text-gray-300"
                    href="https://policies.google.com/privacy"
                  >
                    Política de privacidade
                  </a>
                  {"\u00A0"}e aos{"\u00A0"}
                  <a
                    className="text-gray-400 font-medium hover:text-gray-300"
                    href="https://policies.google.com/terms"
                  >
                    Termos de serviço
                  </a>
                  {"\u00A0"}do Google.
                </div>
              </div>
            </form>
          </div>
        )}
      </div>
      <footer className="absolute bottom-0 w-full flex justify-center text-white opacity-50">
        © Uperttech 2021
      </footer>
    </main>
  );
}
