export async function submitGoogleForm(
  url: string,
  data: FormData
): Promise<void> {
  const response = await fetch(`${url}/formResponse`, {
    method: "POST",
    body: data,
    mode: "no-cors",
  });

  if (!response.ok && response.type !== "opaque") {
    throw new Error("Não foi possível enviar sua mensagem, tente novamente");
  }
}

export const adaptFormData = (
  formData: FormData,
  mappings: Record<string, string>
): FormData => {
  const newFormData = new FormData();

  [...formData.entries()].forEach(
    ([key, value]) => mappings[key] && newFormData.set(mappings[key], value)
  );

  return newFormData;
};
